export default {
  props: {
    required: {
      type: Boolean,
      default: false
    },
    bgTop: {
      type: String,
      default: '0'
    },
    zIndex: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      default: ''
    },
    showSearchDialog: {
      type: Boolean,
      default: true
    }
  },
  components: {

  },
  watch: {
    showSearchDialog(v) {
      this.visible = v;
      if (v) {
        this.isFocus = false
        this.isCancel = true
        this.currentValue = ''
      }
    }
  },
  data() {
    return {
      currentValue: '',
      isCancel: true,
      isFocus: false,
      visible: false
    }
  },
  methods: {
    prevent(event) {
      event.preventDefault()
    },
    beforeEnter: function() {
      // 禁止页面滚动
      this.bodyOverflow = document.body.style.overflow;
      document.body.style.overflow = 'hidden';
      document.body.addEventListener('touchmove', this.prevent);
    },
    leave: function () {
      document.body.style.overflow = this.bodyOverflow;
      document.body.removeEventListener('touchmove', this.prevent);
    },
    closeSearchDialog() {
      this.visible = false
      this.$emit('closeSearchDialog')
    },
    clear () {
      this.currentValue = ''
      this.isFocus = true
      this.setFocus()
    },
    cancel () {
      this.isCancel = true
      this.currentValue = ''
      this.$emit('on-cancel')
    },
    touch () {
      this.isCancel = false
      this.$emit('on-touch')
    },
    setFocus () {
      this.$refs.input.focus()
    },
    setBlur () {
      this.$refs.input.blur()
    },
    onFocus () {
      this.isFocus = true
      this.$emit('on-focus')
      this.touch()
    },
    onBlur () {
      this.isFocus = false
    },
    submitSearch() {
      if (this.currentValue.trim() === '') return
      this.$emit('on-submit', this.currentValue.trim())
    }
  },
  created() {
    this.uuid = Math.random().toString(36).substring(3, 8)
  },
  beforeDestroy() {

  }
}



