import {
  researchService
} from './resources'

/**
 * 返回的是promise
 */
export default {
  /**
   * /purchase/list
   * save 默认是post请求
   *
   */
  getResearchMarketBaseConfig: function(params) {
    return researchService.save({ controller: 'baseconfig', action: 'getResearchMarketBaseConfig' }, params)
  },
  getResearchMarketTask: function (params) {
    return researchService.save({ controller: 'markettask', action: 'getResearchMarketTask' }, params)
  },
  getResearchMarketGoodsInfo: function (params) {
    return researchService.save({ controller: 'markettask', action: 'getResearchMarketGoodsInfo' }, params)
  },
  insertResearchMarketTaskRecord: function (params) {
    return researchService.save({ controller: 'markettaskrecord', action: 'insertResearchMarketTaskRecord' }, params)
  },
  getResearchMarketTaskRecord: function (params) {
    return researchService.save({ controller: 'markettaskrecord', action: 'getResearchMarketTaskRecord' }, params)
  }
}



