
import CHeader from '@/components/x-header'
import CFilterBar from '@/components/filter-bar'
import CSearchBar from '@/components/search-bar'
import CScroll from '@/components/scroll'
import { go } from '@/libs/router'
import domUtil from '@/utils/dom'
import researchApi from '@/api/research'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

const pageSize = 10
const taskTypeMust = 1
const taskTypeWhiteMust = 2
const taskTypeCommon = 3
const PURCHASE_MR_TYPE = 1
const SALE_MR_TYPE = 2
const numericexp = /^([1-9][\d]{0,7}|0)(\.[\d]{0,2})?$/
export default {
components: {
XInput,
FlexboxItem,
Flexbox,
XButton,
CHeader,
CFilterBar,
CSearchBar,
CScroll,
Group,
Cell,
CellBox,
XImg,
TransferDom
},
data() {
return {
scrollerTop:'98px',
data: [],
marketId: 0,
marketName: '',
markupPercentage: 0,
isShow: false,
searchActive: false,
searchParam: '',
searchTop: '98px',
searchUsed: false,
params: {},
noDataText: '没有更多数据',
numericType: function (value) {
return {
valid: numericexp.test(value.trim()),
msg: '价格填写不正确'
}
},
mrType: 0,
scrollbar: true,
scrollbarFade: true,
pullDownRefresh: true,
pullDownRefreshThreshold: 90,
pullDownRefreshStop: 40,
pullUpLoad: true,
pullUpLoadThreshold: 0,
pullUpLoadMoreTxt: '没有数据',
pullUpLoadNoMoreTxt: '没有更多数据'
}
},
created() {
let marketObj = this.$LStorage.getItem('mr_market')
if (!marketObj || !marketObj.id || !((marketObj.id + '') > '0')) {
this.toast('市场信息丢失，请重新选中')
setTimeout(() => {
go('/', this.$router)
})
} else {

if (this.$route.path.indexOf('purchase') > 0) {
this.mrType = PURCHASE_MR_TYPE
} else {
this.mrType = SALE_MR_TYPE
}
if (this.mrType !== marketObj.dataType) {
this.toast('市场信息丢失，请重新选中')
setTimeout(() => {
go('/')
})
}
this.marketId = marketObj.id
this.marketName = marketObj.name
if (marketObj.markupPercentageDouble) {
this.markupPercentage = marketObj.markupPercentageDouble
} else {
this.markupPercentage = 0
}

this.params.dataType = this.mrType
this.params.marketId = marketObj.id
this.params.pageSize = pageSize
this.params.pageNumber = 1
this.params.source = 'app'
}
},
mounted() {
document.body.setAttribute(
'style',
'background-color: #e4e4e4;'
);
this.searchTop = '' + document.querySelector('.vux-header').offsetHeight

this.onPullingUp()
},
beforeDestroy() {
document.body.setAttribute(
'style',
'background-color: #fbf9fe;'
);
},
computed: {
scrollbarObj: function () {
return this.scrollbar ? {fade: this.scrollbarFade} : false
},
pullDownRefreshObj: function () {
return this.pullDownRefresh ? {
threshold: parseInt(this.pullDownRefreshThreshold),
stop: parseInt(this.pullDownRefreshStop)
} : false
},
pullUpLoadObj: function () {
return this.pullUpLoad ? {
threshold: parseInt(this.pullUpLoadThreshold),
txt: {more: this.pullUpLoadMoreTxt, noMore: this.pullUpLoadNoMoreTxt}
} : false
}
},
methods: {
async getPurchaseList(params, callback) {
let resp = await researchApi.getResearchMarketTask(params);
if (resp && resp.body && resp.body !== null) {
let respBody = resp.body
if (this.apiOk(respBody)) {
let respData = this.apiData(respBody)
if (callback) {
callback(respData.data)
}
} else {
this.toast('获取市场市调任务失败' + (respBody.error && respBody.error.msg ? respBody.error.msg : '') )
}
} else {
this.toast('调用获取市场市调任务接口失败')
}
},
onPullingDown() {
let me = this
me.params.pageNumber = 1
me.getPurchaseList(me.params, function (data) {
if (data.rows) {
let rows = data.rows
if (rows.length > 0) {
me.data = rows
if (rows.length <= pageSize) {
me.params.pageNumber++
}
} else {
me.data = []
me.$nextTick(() => {
me.$refs.scroll.forceUpdate(true)
})
}
} else {
this.toast('获取市场市调任务失败')
}
})
},
onPullingUp() {

let me = this
me.getPurchaseList(me.params, function (data) {
if (data.rows) {
let rows = data.rows
if (rows.length > 0) {
me.data = me.data.concat(rows)
if (rows.length <= pageSize) {
me.params.pageNumber++
}
} else {
if (me.params.pageNumber === 1) {
me.$nextTick(() => {
me.$refs.scroll.forceUpdate(true)
})
} else {
me.$nextTick(() => {
me.$refs.scroll.forceUpdate(false)
})
}
}
} else {

if (me.params.pageNumber === 1) {
me.$nextTick(() => {
me.$refs.scroll.forceUpdate(true)
})
} else {
me.$nextTick(() => {
me.$refs.scroll.forceUpdate(false)
})
}
}
})
},
submitItem(item, index) {
let me = this
if (!item.purchase_price) {
me.toast('请填写进价')
return
}
if (!item.stock_price) {
me.toast('请填写倒库价')
return
}
if (!numericexp.test(item.purchase_price)) {
me.toast('进价格式不正确')
return
}
if (!numericexp.test(item.stock_price)) {
me.toast('倒库价格式不正确')
return
}
me.callSubmit(item, index, function (item, index) {
me.data.splice(index, 1)
})
me.onPullingDown()
},
async callSubmit(item, index, callback) {

let submitParams = {}
submitParams.class1Id = item.class1Id
submitParams.class2Id = item.class2Id
submitParams.class3Id = item.class3Id
submitParams.taskId = item.id
submitParams.skuId = item.skuId
submitParams.researchPriceDouble = item.purchase_price
submitParams.inventoryPriceDouble = item.stock_price
submitParams.marketId = item.marketId
submitParams.dataType = item.dataType
submitParams.priceDouble = item.priceDouble
let resp = await researchApi.insertResearchMarketTaskRecord(submitParams)
if (resp && resp.body && resp.body != null) {
let respBody = resp.body
if (this.apiOk(respBody)) {
this.toast('操作成功')
if (callback) {
callback(item, index)
}
} else {
this.toast(respBody.error && respBody.error.msg ? respBody.error.msg : '操作失败')
}
} else {
this.toast('操作失败')
}
},
keyupPurchsePrice (item, index, v) {
if ('' === v) {
if (item.self_stock !== 1) {
item.stock_price = ''
this.data.splice(index, 1, item)
}
} else if (!numericexp.test(v)) {

} else {
if (item.self_stock !== 1) {
item.stock_price = (item.purchase_price * this.markupPercentage).toFixed(2)
this.data.splice(index, 1, item)
}
}
},
keyupStockPrice(item, index, v) {
if ('' === v) {
item.self_stock = 0
} else {
item.self_stock = 1
}
this.data.splice(index, 1, item)
},
isMustMrTaskType(type) {
return type &&
(type === taskTypeMust || type === (taskTypeMust + '')
|| type === taskTypeWhiteMust || type === (taskTypeWhiteMust + ''))
},
isHandledMrTask(v) {
return v && ((v + '') > '0')
},
getMrTaskTypeName(v, type) {
if (this.isHandledMrTask(v)) {
return '已调'
} else if (type === taskTypeMust || type === (taskTypeMust + '')) {
return '必调'
} else if (type === taskTypeWhiteMust || type === (taskTypeWhiteMust + '')) {
return '白名单必调'
} else {
return '选调'
}
},
onClickDetail(skuId) {
go('/purchase/detail/' + skuId, this.$router)
},
onClickRecord() {
go('/purchase/market/record', this.$router)
},
handleShowDialog () {
this.isShow = !this.isShow
},
handleCloseDialog () {
this.isShow = false
},
handleSelectAll() {
delete this.params.skuInfo
delete this.params.class1IdList
delete this.params.taskTypeList
this.onPullingDown()
this.$refs.myfilter.selectAll()
},
changeSelect(sClass1, sTaskType) {

this.searchUsed = false
delete this.params.skuInfo
if (sClass1.length > 0) {
this.params.class1IdList = sClass1
} else {
delete this.params.class1IdList
}
if (sTaskType.length > 0) {
this.params.taskTypeList = sTaskType
} else {
delete this.params.taskTypeList
}

this.onPullingDown()
},

onSearch() {
this.searchActive = !this.searchActive


},
handleCloseSearchDialog() {
this.searchActive = false
},
handleSubmitSearch(v) {
this.searchActive = false
this.searchUsed = true
this.params.skuInfo = v
this.onPullingDown()
},
handleScroll() {
var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
if (scrollTop >= 95) {
document.body.scrollTop = 0
}
},
handleInput(i) {

}
}
}



