
import XHeader from 'vux/src/components/x-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
props: {
title: String,
history: { type: Boolean, default: true },
back_text: { type: String, default: '返回' },
goBack: { type: Function, default: null },
showRight: {
type: Boolean,
default: false
},
preventGoBackSet: {
type: Boolean,
default: false
}
},
components: {
XHeader
},
methods: {
onClickGoBack () {
if (this.preventGoBackSet) {
this.$emit('on-click-go-back')
} else {
this.$router ? this.$router.back() : window.history.back()
}
},
onClickRight() {
console.log('search in x-header')
this.$emit('on-search')
}
}
}



