import {
  classService
} from './resources'

/**
 * 返回的是promise
 */
export default {
  /**
   * /purchase/list
   * save 默认是post请求
   *
   */
  getClass1: function(params) {
    return classService.save({ controller: 'public', action: 'getClass1' }, params)
  }
}



