import Vue from 'vue'
import VueResource from 'vue-resource'
import { APIURL } from '../config/'
import LStorage from '@/utils/LStorage'

Vue.use(VueResource)
Vue.http.options.xhr = { withCredentials: true }
Vue.http.options.emulateHTTP = true
Vue.http.options.responseType = 'json' //发送请求前
Vue.http.headers.common['Content-Type'] = 'application/json'
Vue.http.headers.common['Accept'] = 'application/json'
Vue.http.interceptors.push(function(request, next) {
  try {
    let tk = LStorage.getItem('mr_token')
    if (tk && tk.length > 0) {
      request.url = APIURL + request.url + '?user_token=' + tk
      next(function(response) {
        if (response.status != 200) {
          Vue.$vux.loading.hide()
          Vue.$vux.toast.text('接口请求失败')
          throw new Error('接口请求失败')
        }
        //没有权限，统一登出
        if (response.body && response.body.error_code && response.body.error_code === 1009) {
          let message = { backClick: 0, logOut: 1 }
          window.parent.postMessage(JSON.stringify(message), '*')
          LStorage.removeItem('mr_token')
          Vue.$vux.toast.text('没有登录')
          throw new Error('没有登录')
        }
        Vue.$vux.loading.hide()
      })
    } else {
      let message = { backClick: 0, logOut: 1 }
      window.parent.postMessage(JSON.stringify(message), '*')
      Vue.$vux.toast.text('没有登录')
      throw new Error('没有登录')
    }
  } catch (e) {
    Vue.$vux.loading.hide()
    throw new Error(e)
  }
})

/**
 * 订单类相关
 */
export const researchService = Vue.resource('/research{/controller}{/action}')
export const classService = Vue.resource('/priceresearch{/controller}{/action}')



