
import classApi from '@/api/class-rpc'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

const defaultClass1Index = 0
const defaultTaskTypeIndex = 0
const taskTypeMust = 1
const taskTypeWhiteMust = 2
const taskTypeCommon = 3
export default {
components: {
FlexboxItem,
Flexbox,
XButton
},
props: {
showDialog: {
type: Boolean,
default: true
},
filterTop: {
type: String
}
},
data() {
return {
visible: false,
bgTop: '98px',
sClass1: [],
sTaskType: [],
class1List: [],
taskTypeList: [
{
'id': taskTypeMust,
'name': '必调'
}, {
'id': taskTypeWhiteMust,
'name': '白名单必调'
}, {
'id': taskTypeCommon,
'name': '选调'
}
]
}
},
mounted() {

let me = this
let defaultClass1Item = {
'id': 0,
'name': '全部',
'selectedIndex': defaultClass1Index
}
me.class1List.splice(defaultClass1Index, 0, defaultClass1Item);
this.getClass1({}, function(data) {
if (data && data.length > 0) {
me.class1List = me.class1List.concat(data)
for (let i = 0; i < me.class1List.length; i++) {
if (i !== defaultClass1Index) {
let class1Item = me.class1List[i];
class1Item.selectedIndex = -1;
me.class1List.splice(i, 1, class1Item);
}
}
} else {
me.toast('没有一级分类')
}
})


let defaultTaskTypeItem = {
'id': 0,
'name': '全部',
'selectedIndex': defaultTaskTypeIndex
}
me.taskTypeList.splice(defaultTaskTypeIndex, 0, defaultTaskTypeItem);
for (let j = 0; j < me.taskTypeList.length; j++) {
if (j !== defaultTaskTypeIndex) {
let taskTypeItem = me.taskTypeList[j];
taskTypeItem.selectedIndex = -1;
me.taskTypeList.splice(j, 1, taskTypeItem);
}
}
console.log(me.class1List);
console.log(me.taskTypeList);
},
watch: {
showDialog(v) {
this.visible = v
if (v) {

this.initData()
}
}
},
methods: {
initData() {

let class1Param = this.sClass1;
if (class1Param.length === 0) {

for (let i = 0; i < this.class1List.length; i++) {
let class1Item = this.class1List[i];
if (i === defaultClass1Index) {
class1Item.selectedIndex = defaultClass1Index;
} else {
class1Item.selectedIndex = -1;
}
this.class1List.splice(i, 1, class1Item);
}
} else {
for (let i = 0; i < this.class1List.length; i++) {
let class1Item = this.class1List[i];
let class1Id = class1Item.id;
let classInParam = false;
for (let j = 0; j < class1Param.length; j++) {
if (class1Id === class1Param[j] || class1Id === '' + class1Param[j] + '') {
classInParam = true;
break;
}
}
if (classInParam) {
class1Item.selectedIndex = i;
} else {
class1Item.selectedIndex = -1;
}
this.class1List.splice(i, 1, class1Item);
}
}

let taskTypeParam = this.sTaskType;
if (taskTypeParam.length === 0) {

for (let i = 0; i < this.taskTypeList.length; i++) {
let taskTypeItem = this.taskTypeList[i];
if (i === defaultTaskTypeIndex) {
taskTypeItem.selectedIndex = defaultTaskTypeIndex;
} else {
taskTypeItem.selectedIndex = -1;
}
this.taskTypeList.splice(i, 1, taskTypeItem);
}
} else {
for (let i = 0; i < this.taskTypeList.length; i++) {
let taskTypeItem = this.taskTypeList[i];
let taskTypeId = taskTypeItem.id;
let taskTypeInParam = false;
for (let j = 0; j < taskTypeParam.length; j++) {
if (taskTypeId === taskTypeParam[j] || taskTypeId === '' + taskTypeParam[j] + '') {
taskTypeInParam = true;
break;
}
}
if (taskTypeInParam) {
taskTypeItem.selectedIndex = i;
} else {
taskTypeItem.selectedIndex = -1;
}
this.taskTypeList.splice(i, 1, taskTypeItem);
}
}
},
async getClass1(params, callback) {
let result = await classApi.getClass1(params);
if (result && result.body && result.body !== null) {
if (this.apiOk(result.body)) {
let data = this.apiData(result.body)
if (callback) {
callback(data.data)
}
} else {
this.toast('获取一级分类失败')
}
} else {
this.toast('调用获取一级分类接口失败')
}
},

closeDialog() {
this.visible = false
this.$emit('closeDialog')
},

handleEnsure() {

let class1Param = this.sClass1 = []
for (let i = 0; i < this.class1List.length; i++) {
if (i !== defaultClass1Index) {
let tClass1Item = this.class1List[i]
if (tClass1Item.selectedIndex === i) {
let class1Id = parseInt(tClass1Item.id)
class1Param.push(class1Id)
}
}
}
if (class1Param.length > 0) {
this.sClass1 = class1Param
} else {
this.sClass1 = []
}

let taskTypeParam = this.sTaskType = []
for (let i = 0; i < this.taskTypeList.length; i++) {
if (i !== defaultTaskTypeIndex) {
let tTaskTypeItem = this.taskTypeList[i]
if (tTaskTypeItem.selectedIndex === i) {
let taskTypeId = parseInt(tTaskTypeItem.id)
taskTypeParam.push(taskTypeId)
}
}
}
if (taskTypeParam.length > 0) {
this.sTaskType = taskTypeParam
} else {
this.sTaskType = []
}
this.$emit('changeSelect', this.sClass1, this.sTaskType);
this.closeDialog()
},

handleCancel() {
this.closeDialog();
},
selectAll() {
this.sClass1 = []
this.sTaskType = []
},
prevent(event) {
event.preventDefault()
},
beforeEnter: function(el) {

this.bodyOverflow = document.body.style.overflow;
document.body.style.overflow = 'hidden';

document.querySelector('#main').addEventListener('touchmove', this.prevent);
},
leave: function (el) {
document.body.style.overflow = this.bodyOverflow;

document.querySelector('#main').removeEventListener('touchmove', this.prevent);
},
clickFilterClass1(class1Item, index) {
if (index === defaultClass1Index) {

for (let j = 0; j < this.class1List.length; j++) {
let tClass1Item = this.class1List[j];
if (j === index) {
tClass1Item.selectedIndex = index;
} else {
tClass1Item.selectedIndex = -1;
}
this.class1List.splice(j, 1, tClass1Item);
}
} else {

if (class1Item.selectedIndex === index) {
let existOther = false;
for (let i = 0; i < this.class1List.length; i++) {
if (i !== defaultClass1Index && i !== index) {
let tClass1Item = this.class1List[i];
if (tClass1Item.selectedIndex === i) {
existOther = true;
break;
}
}
}
class1Item.selectedIndex = -1;
this.class1List.splice(index, 1, class1Item);
let defaultClass1Item = this.class1List[defaultClass1Index];
if (existOther) {
if (defaultClass1Item.selectedIndex === defaultClass1Index) {
defaultClass1Item.selectedIndex = -1;
this.class1List.splice(defaultClass1Index, 1, defaultClass1Item);
}
} else {
defaultClass1Item.selectedIndex = defaultClass1Index;
this.class1List.splice(defaultClass1Index, 1, defaultClass1Item);
}
} else {
let defaultClass1Item = this.class1List[defaultClass1Index];
defaultClass1Item.selectedIndex = -1;
this.class1List.splice(defaultClass1Index, 1, defaultClass1Item);
class1Item.selectedIndex = index;
this.class1List.splice(index, 1, class1Item);
}
}
},
clickFilterTaskType(taskTypeItem, index) {
if (index === defaultTaskTypeIndex) {

for (let j = 0; j < this.taskTypeList.length; j++) {
let tTaskTypeItem = this.taskTypeList[j];
if (j === index) {
tTaskTypeItem.selectedIndex = index;
} else {
tTaskTypeItem.selectedIndex = -1;
}
this.taskTypeList.splice(j, 1, tTaskTypeItem);
}
} else {

if (taskTypeItem.selectedIndex === index) {
let existOther = false;
for (let i = 0; i < this.taskTypeList.length; i++) {
if (i !== defaultTaskTypeIndex && i !== index) {
let tTaskTypeItem = this.taskTypeList[i];
if (tTaskTypeItem.selectedIndex === i) {
existOther = true;
break;
}
}
}
taskTypeItem.selectedIndex = -1;
this.taskTypeList.splice(index, 1, taskTypeItem);
let defaultTaskTypeItem = this.taskTypeList[defaultTaskTypeIndex];
if (existOther) {
if (defaultTaskTypeItem.selectedIndex === defaultTaskTypeIndex) {
defaultTaskTypeItem.selectedIndex = -1;
this.taskTypeList.splice(defaultTaskTypeIndex, 1, defaultTaskTypeItem);
}
} else {
defaultTaskTypeItem.selectedIndex = defaultTaskTypeIndex;
this.taskTypeList.splice(defaultTaskTypeIndex, 1, defaultTaskTypeItem);
}
} else {
let defaultTaskTypeItem = this.taskTypeList[defaultTaskTypeIndex];
defaultTaskTypeItem.selectedIndex = -1;
this.taskTypeList.splice(defaultTaskTypeIndex, 1, defaultTaskTypeItem);
taskTypeItem.selectedIndex = index;
this.taskTypeList.splice(index, 1, taskTypeItem);
}
}
}
}
}



